import * as THREE from 'three'
import { clearInterval } from 'timers'
let pn=()=>{return Math.random()>0.5?1:-1}
class Dot{
    constructor(scene,name,pt,vec,type='btn-neutral'){
        this.type = type
        this.name = name
        this.scene = scene,
        this.mesh = null,
        this.oriScale=null,
        this.shouldRemove = false
        
        switch (this.type) {
            case 'btn-neutral':
                this.loc = new THREE.Vector3(2,-1,10)
            break;
            case 'btn-happy':
                this.loc = new THREE.Vector3(10,-1,2)
            break;
            case 'btn-sad':
                this.loc = new THREE.Vector3(5,1,5)
            break;
            case 'btn-surprised':
                this.loc = new THREE.Vector3(-5,-1,5)
            break;
            case 'btn-angry':
                this.loc = new THREE.Vector3(-10,1,2)
            break;
            case 'btn-disgusted':
                this.loc = new THREE.Vector3(-5,1,-5)
            break;
            case 'btn-fearful':
                this.loc = new THREE.Vector3(10,0,5)
            break;
        }

        // this.loc = new THREE.Vector3(pt.x,pt.y,pt.z),
        this.vec = new THREE.Vector3(0,0,0),
        this.hold = 1
        this.oriVec = this.vec
        this.oriLoc = new THREE.Vector3(pt.x,pt.y,pt.z)
        this.maxVec = new THREE.Vector3(1,1,1)
        this.minVec = new THREE.Vector3(-1,-1,-1)
        this.acc = new THREE.Vector3(0.01,0.01,0.01)
        this.speedPara = vec
        this.speedParaLoop = null
        this.speedParaLoopTrue = true
        this.fric = new THREE.Vector3(0.01,0.01,0.01)
        this.tarPoint = new THREE.Vector3(0,0,0)
        this.vertices = []
        this.tars = []
        this.mouseX = null,
        this.mouseY = null
        setTimeout(() => {
            this.acc = new THREE.Vector3(-1,-1,-1)
            this.maxVec = new THREE.Vector3(0.1,0.1,0.1)
            this.speedPara = 300
        }, 1250);
        
   
        
    }
    setTarget(x){
        if(Math.random()>0.5){
            this.tarPoint = new THREE.Vector3(x,0,x)
        }else{
            this.tarPoint = new THREE.Vector3(x,0,x)
        }
    }
    concrete(){
        this.tarPoint = this.oriLoc
        this.hold = Math.random()*3
        this.maxVec = new THREE.Vector3(0.02,0.02,0.02)
    }
    unconcrete(){
        this.hold = Math.sin(Math.random())*this.hold*pn()
        this.speedPara = 50
        this.minVec =new THREE.Vector3(-50,-50,-50)
        this.maxVec = new THREE.Vector3(50,50,50)
        let back = setTimeout(()=>{
            this.speedPara = 300
            this.hold = 1
            this.minVec =new THREE.Vector3(-1/10,-1/10,-1/10)
            this.maxVec = new THREE.Vector3(1/10,1/10,1/10)

        },500)
    }
    draw(){
        var spriteMap
        var rNF=(x)=>{return Math.floor(Math.random()*x)}

        switch (this.type) {
            case 'btn-neutral':
                switch (rNF(4)) {
                    case 0:
                        spriteMap = new THREE.TextureLoader().load( 'models/segmentB.png' );
                        break;
                    case 1:
                        spriteMap = new THREE.TextureLoader().load( 'models/segmentC.png' );
                        break;
                    case 2:
                        spriteMap = new THREE.TextureLoader().load( 'models/personSlice.png' );
                        break;
                    default:
                        spriteMap = new THREE.TextureLoader().load( 'models/segmentC.png' );
                        break;
                }
            break;
            case 'btn-happy':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentC.png' );
            break;
            case 'btn-sad':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentF.png' );
            break;
            case 'btn-surprised':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentG.png' );
            break;
            case 'btn-angry':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentB.png' );
            break;
            case 'btn-disgusted':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentH.png' );
            break;
            case 'btn-fearful':
                spriteMap = new THREE.TextureLoader().load( 'models/segmentE.png' );
            break;
        
            default:
                break;
        }

        
       
        
        var spriteMaterial = new THREE.SpriteMaterial( { map: spriteMap, color: 0xffffff*Math.random() } );
        var spritePerson = new THREE.Sprite( spriteMaterial );
        let scale = 0.5+ Math.random()
        this.oriScale = scale
        spritePerson.scale.set(scale,scale,1)
        spritePerson.position.set(this.loc.x,this.loc.y,this.loc.z)
        spritePerson.name = this.name
        this.mesh = spritePerson
        this.scene.add( this.mesh );
        

    
    }
    
    update(x,y){
        this.tarPoint = new THREE.Vector3(x*this.hold,0*this.hold,y*this.hold)
        this.acc = this.tarPoint.sub(this.loc).normalize().divideScalar(this.speedPara)
        this.vec = this.vec.add(this.acc)
        this.vec = this.vec.clamp(this.minVec,this.maxVec)
        // this.vec = this.vec.sub(this.fric).min(new THREE.Vector3(0,0,0))
        
        
        this.loc = this.loc.add(this.vec)
        
        this.mesh.position = this.loc
        this.mesh.updateMatrix()

    }
}

