let perlin = require('./perlin');
function rNF(x) {
    return Math.floor(Math.random() * x)
}

const coor = (ori, to, add=true) => {
   
    var newPt
    if(add === true){
        if (Array.isArray(ori)) {
            newPt = Array.from(ori)
            if (to.length === 1) {
                to[1] = to[0]
            }
            newPt[0] += to[0]
            newPt[1] += to[1]
        } else {
            newPt = Object.assign({}, ori);
            newPt.x += to.x
            newPt.y += to.y
        }
    }else{
        if (Array.isArray(ori)) {
            newPt = Array.from(ori)
            if (to.length === 1) {
                to[1] = to[0]
            }
            newPt[0] -= to[0]
            newPt[1] -= to[1]
        } else {
            newPt = Object.assign({}, ori);
            newPt.x -= to.x
            newPt.y -= to.y
        }
    }
    
    return newPt
}

const pn = () => {
    return Math.random() > 0.5 ? 1 : -1
}

function randomG(v) {
    var r = 0;
    for (var i = v; i > 0; i--) {
        r += Math.random();
    }
    return r / v;
}





function rMinMax(min, max) {
    return Math.random() * (+max - +min) + +min;
}

function getMainColor(hsl){
    let color = new Color.random()
    color.hue = hsl.hue * 360
    color.saturation = hsl.saturation
    color.lightness = hsl.lightness
    return color
}

let Methods = {
    onNeutral(proID,hsl,val){
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
                project.clear()
            }
        })
        let ran = Math.floor(val*10)
        const count = {
            x: ran,
            y: ran
        }
        let g = new Group()
        let color = getMainColor(hsl)
        perlin.noise.seed(Math.random())
        for (let y = 0; y < count.y; y++) {
            for (let x = 0; x < count.x; x++) {
                
                let rect = new Path.Rectangle([0, 0], [100, 100])
                let cpColor = color.clone()
                cpColor.saturation += perlin.noise.perlin2(x/10,y/10)
                cpColor.lightness -= perlin.noise.perlin2(x/10,y/10)
                rect.fillColor = cpColor
                
            
                rect.position.x = x * 100
                rect.position.y = y * 100
                g.addChild(rect)
            }
        }
        g.fitBounds(view.bounds)
    },
    
    onDisgusted(proID,hsl,val){
        // console.log(hsl);
        
         paper.projects.forEach((pro) => {
             if (pro.name == proID) {
                 pro.activate()
                 project.clear()
             }
         })
         const count = {
             x: Math.floor(val*10),
             y: Math.floor(val*10)
         }
         let g = new Group()
         let color = getMainColor(hsl)
         perlin.noise.seed(Math.random())
         for (let y = 1; y < count.y+1; y++) {
             for (let x = 1; x < count.x+1; x++) {
                let g2 = new Group()

                 let cir = new Path.Circle([0,0],50)
                 let cpColor = color.clone()
                    cpColor.saturation += perlin.noise.perlin2(x/10,y/10)
                    cpColor.lightness -= perlin.noise.perlin2(x/10,y/10)
                    cir.fillColor = cpColor
                    
                    
                    g2.addChild(cir)
                    let circleCount = Math.max(3,rNF(6))
                    for(let i=1;i<circleCount;i++){
                        let cirSmall = new Path.Circle([0,0],i*5)
                        cirSmall.fillColor = cpColor
                        cirSmall.fillColor.lightness += perlin.noise.perlin2(i/10,i/100)
                        cirSmall.fillColor.hue += perlin.noise.perlin2(i/10,i/100)
                        // cirSmall.fitBounds(g2.bounds)
                        g2.addChild(cirSmall)
                    }
                    g2.position.x = x * 100
                    g2.position.y = y * 100
                    
                    g.addChild(g2)
             }
         }
         g.fitBounds(view.bounds)
    },

    onSad(proID,hsl,val){
        
        let intense = [val-0.7]/0.7*100
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
            }
        })
        project.clear()
        let [w, h] = [project.view.bounds.width, project.view.bounds.height]
        let color = getMainColor(hsl)

        
        // let bg= new Path.Rectangle([0,0],[view.bounds.width,view.bounds.height])
        // bg.fillColor = color.clone()
        // bg.fillColor.lightness -= 0.3
        // tool should be copy

        let gAll = new Group()
        let x = 0
        
        do{
            let y = 0
            let g = new Group
            let width = rMinMax(100,w/2)
            do{
                let height
                Math.random() < intense/50 ? height = rMinMax(5, Math.random() * h / 10) : height = width
                let path = Path.Rectangle([0, 0], [width, height])
                path.style = {
                    fillColor: color.clone()
                }
                path.fillColor.saturation += Math.random()*pn()
                path.fillColor.brightness += Math.random()*pn()
                if (path.fillColor.brightness < 0.3) path.fillColor.brightness = 0.6
                if (path.fillColor.brightness > 0.8) path.fillColor.brightness = 0.7


                let lastChild = path.previousSibling
                if (y !== 0) {
                    path.bounds.top = lastChild.bounds.bottom
                }
                g.addChild(path)
                // IMPORTANT
                
                y+= height

                // IMPORTANT
            }while(y < h)
            let lastChild = g.previousSibling
            if (x !== 0) {
                g.bounds.left = lastChild.bounds.right
            }
            x+= width
            gAll.addChild(g)
        }while(x < w)

        gAll.children.forEach((g)=>{
            g.lastChild.bounds.height = project.activeLayer.bounds.bottom - g.lastChild.bounds.top
        })
        // gAll.bounds.width = w*0.85
        // gAll.bounds.height = h*0.85
        // gAll.bounds.center.x = w/2
        // gAll.bounds.center.y = h / 2
        for(let i=0;i<10;i++){
            let ranG = gAll.children[rNF(gAll.children.length)]
            let ranBox = ranG.children[rNF(ranG.children.length)]

            let bd = ranBox.bounds
            switch (rNF(7)) {
                case 1:
                    //TODO 画圆
                    let r = Math.min(bd.width, bd.height) / 2
                    let path = new Path.Circle(ranBox.bounds.center, r - 10)
                    path.fillColor = ranBox.fillColor ? ranBox.fillColor : Color.random()
                    path.fillColor.hue += 180
                    
                    break;
                case 2:
                    if(bd.height < 100) return
                    let width = 5
                    let height = width/3
                    let count = 5
                    let g = new Group()
                    var angle = 45*pn()

                    for(let y=0;y<count;y++){
                        let spaceY = [bd.height - height * count] / count
                        for (let x = 0; x < count; x++) {
                            let spaceX = [bd.width-width*count]/count
                            let rec = new Path.Rectangle(bd.topLeft,[width,height])
                            rec.fillColor = ranBox.fillColor ? ranBox.fillColor : Color.random()
                            
                            rec.fillColor.brightness -= 0.5

                            rec.position.x += x * width + x*spaceX
                            rec.position.y += y * height + y * spaceY
                            rec.rotation = angle
                            g.addChild(rec)
                        }
                    }
                    g.bounds.center = bd.center
                    // g.blendMode = "divide"
                case 3:
                
                    let borderW, borderH, defaultWidth=5
                    switch (bd.width > bd.height) {
                        case true:
                            borderW = bd.width
                            borderH = defaultWidth
                            break;
                    
                        default:
                            borderH = bd.height
                            borderW = defaultWidth
                            break;
                    }
                    let rec = new Path.Rectangle(bd.topLeft, [borderW, borderH])
                    pn() > 0 ? rec.fillColor = 'white' : rec.fillColor = Color(8,9,26,0.5)
                    break;
                case 4:
                
                    const v = rNF(10)+10
                    switch (rNF(3)) {
                        case 0:
                            bd.top -= v
                            break;
                        case 1:
                            bd.left -=v
                            break;
                        case 3:
                            bd.right += v
                        break;
                        case 4:
                            bd.bottom += v
                        break;
                        default:
                            break;
                    }
                    break;
                case 5:
                    let dis = 50*pn()
                    let rec2 = new Path.Rectangle(bd.topLeft,bd.size)
                    rec2.fillColor = ranBox.fillColor ? ranBox.fillColor : Color.random()
                    rec2.fillColor.brightness += 0.2*pn()
                    rec2.scale(0.9)
                    coor(rec2.position,{x:dis,y:dis})
                case 6:
                    let lineTtoR = new Path.Line(bd.topLeft,bd.bottomRight)
                    lineTtoR.style={
                        strokeColor:'#1d1d2f',
                        strokeWidth:5,
                        strokeCap:'round'
                    }
                    lineTtoR.blendMode = 'darken'
                    pn() > 0 ? lineTtoR.scale.x = 1 : lineTtoR.scale.x=-1
                    break;
                default:
                    ranBox.rasterize().scale(1)
                    // ranBox.blendMode = "overlay"
                    break;
            }
        

        }
        
        gAll.fitBounds(view.bounds)
        gAll.scale(1.5)
        

    },

    onHappy(proID,hsl,val){
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
                project.clear()
            }
        })
        let color = getMainColor(hsl)

        let bg= new Path.Rectangle([0,0],[view.bounds.width,view.bounds.height])
        bg.fillColor = color.clone()
        bg.fillColor.lightness -= 0.3
        let g= new Group()
        let count = {x:rMinMax(5,val*15)}
        for(let i=1;i<count.x;i++){
            let cir = new Path.Circle([0,0],count.x/i*30)
            cir.fillColor = color.clone()
            cir.fillColor.hue += perlin.noise.perlin2(i/10,i/100)*10
            cir.fillColor.lightness += perlin.noise.perlin2(i/10,i/100)
            g.addChild(cir)
            // cir.bounds.center = g.bounds.center
        }
        g.fitBounds(view.bounds)
    },

    onFear(proID,hsl,num){
        if (num == null) num = 3
        num = Math.floor([num-0.4]*10)
        // console.log(num);
        
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
                project.clear()
            }
        })

        let [w, h] = [project.view.bounds.width, project.view.bounds.height]
        let vBD = view.bounds
        let color = getMainColor(hsl)

        let g = new Group()
        let countX = Math.max(2,num)
        let countY = Math.max(2,num)
        let size = Math.min(w, h) / Math.min(countX,countY)
        let makeMore = 0
        w > h ? countX += makeMore : countY += makeMore
        let grids = new Group()

        for(let y=0;y<countY;y++){
            if (color.saturation>0.25) color.saturation -= 0.25


            for (let x = 0; x < countX; x++) {
                let colorX = color.clone()
                colorX.hue += 180
                colorX.brightness > 0.5 ? colorX.brightness -= 0.5 : colorX.brightness += 0.5
                let rec = new Path.Rectangle([0,0],[size,size])
                rec.style={
                    fillColor: pn() > 0 ? color : colorX
                }
                let pats = new Group()
                

                
                switch (rNF(4)) {
                    case 0:
                        let width = rMinMax(1.5, 5)
                        let pathA = new Path.Rectangle([0, 0], [size, size / width])
                        pathA.fillColor = pn() > 0 ? colorX : color
                        pathA.bounds.center = rec.bounds.center

                        let pathB = new Path.Rectangle([0, 0], [size / width, size])
                        pathB.fillColor = pn() > 0 ? colorX : color
                        pathB.bounds.center = rec.bounds.center

                        pathA.blendMode = 'difference'
                        pathB.blendMode = 'difference'
                        pats.addChild(pathA)
                        pats.addChild(pathB)
                        pats.rotation = pn() > 0 ? 45 : 0
                        // pats.filBounds(rec.bounds)
                        
                        break;
                    case 1:
                        let pathC = new Path.Circle(rec.bounds.center,size/2.1)
                        pathC.fillColor = pn() > 0 ? colorX : color
                        pathC.bounds.center = rec.bounds.center
                        pats.addChild(pathC)
                        pathC.blendMode = 'difference'

                        let pathD = new Path.Circle(rec.bounds.center, size / 2.1)
                        pathD.fillColor = pn() > 0 ? colorX : color
                        if (pn() > 0) {pathD.bounds.top -= rec.bounds.height / 2 * pn()}else{
                            pathD.scale(0.8)
                        }
                        pats.addChild(pathD)
                        pathD.blendMode = 'difference'
                        let pathE = new Path.Circle(rec.bounds.center, size / 2.1)
                        pathE.fillColor = pn() > 0 ? colorX : color
                        if(pn()>0){pathE.bounds.left -= rec.bounds.width / 2 * pn()}else{
                            pathE.scale(0.8)
                        }
                        pats.addChild(pathE)
                        pathE.blendMode = 'difference'
                        break;
                    case 2:
                        var pathF = new Path.RegularPolygon(rec.bounds.center, 3, size/2);
                        pathF.fillColor = pn()>0?color:colorX
                        pathF.fitBounds(rec.bounds)
                        pathF.bounds.bottom = rec.bounds.bottom
                        pathF.blendMode = 'difference'
                        pn() > 0 ? pats.addChild(pathF) : pathF.remove()

                        
                        var pathG = new Path.RegularPolygon(rec.bounds.center, 3, size / 2);
                        pathG.fillColor = pn() > 0 ? color : colorX
                        pathG.rotation = 180
                        pathG.fitBounds(rec.bounds)
                        pathG.bounds.top = rec.bounds.top
                        pathG.blendMode = 'difference'
                        pn()>0 ? pats.addChild(pathG):pathG.remove()
                        break;
                    default:
                        break;
                }
                
                let grid= new Group(rec.clone(),rec,pats)
                grid.clipped = true
                
                grid.position.x = x * size
                grid.position.y = y * size
                grids.addChild(grid)
            }
        }

        grids.fitBounds(vBD)
        // grids.bounds.center = vBD.center

    },

    onSurprised(proID,hsl,val){
        
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
                project.clear()
            }
        })
        let color = getMainColor(hsl)
        let flowers = new Group()
        const countFlower = { x: 2, y: 2 }

        for (let y = 0; y < countFlower.y; y++) {
            for (let x = 0; x < countFlower.x; x++) {
                const count = 15
                let flower = new Group()
                for (let cen = 1; cen < count; cen++) {
                    perlin.noise.seed(Math.random());
                    let cir = Path.Circle([0, 0], cen * 10)
                    cir.strokeColor = 'red'
                    cir.bounds.center = view.bounds.center

                    const amount = 3 * cen
                    const len = cir.length
                    const path = new Path()
                    //  path.strokeColor = 'black'
                    for (let i = 0; i < amount; i++) {
                        let offset = i / amount * len
                        let pt = cir.getPointAt(offset)
                        path.add(pt)
                    }

                    cir.remove()


                    path.segments.forEach((i, index) => {
                        //  console.log(i)
                        i.point.x += perlin.noise.perlin2(index / 0.5, index / 5) * cen
                        i.point.y += perlin.noise.perlin2(index / 10, index / 5) * cen
                    })
                    path.closed = true
                    path.bounds.center.x += perlin.noise.perlin2(cen / 100, cen / 10) * cen * 10
                    path.bounds.center.y += perlin.noise.perlin2(cen / 10, cen / 100) * cen * 10

                    path.smooth('continous')
                    path.fillColor = color
                    path.fillColor.saturation -= 0.1
                    path.fillColor.hue += perlin.noise.perlin2(cen / 100, cen / 10) * 100
                    path.fillColor.saturation += perlin.noise.perlin2(cen / 100, cen / 10) / 2
                    path.fillColor.brightness -= perlin.noise.perlin2(cen / 100, cen / 10) / 3
                    path.blendMode = 'hue'
                    flower.addChild(path)
                }

                // flower.bounds.selected = true

                // let line = new Path.Arc(flower.bounds.center,[flower.bounds.center.x+rNF(50)*pn(),flower.bounds.center.y+100],[flower.bounds.center.x,flower.bounds.center.y+500])
                // line.strokeColor = {
                //     gradient: {
                //         stops: ['green', 'blue', 'red']
                //     },
                //     origin: line.bounds.topLeft,
                //     destination: line.bounds.bottomRight
                // }
                // // line.sendToBack()
                // line.strokeWidth = 3
                // flower.insertChild(line)
                flower.scale(2+perlin.noise.perlin2(x / 10, y / 10) * 3)
                flower.position.x = view.bounds.center.x + perlin.noise.perlin2(x / 10, y / 5) * 1000
                flower.position.y = view.bounds.center.y + perlin.noise.perlin2(x / 3, y / 10) * 1000


                flowers.addChild(flower)
            }
        }

        flowers.bounds.center = view.bounds.center
        // flowers.fitBounds(view.bounds)

        let bgc = new Path.Rectangle([0, 0], view.bounds.size)
        // bgc.fillColor = 'white'
        // bgc.fillColor.brightness = 0.
        bgc.sendToBack()
        // console.log(bgc);
    }
}

export default Methods