const rb = ()=>{
    return pn() > 0
}
const coor = (ori, to, add=true) => {
   
    var newPt
    if(add === true){
        if (Array.isArray(ori)) {
            newPt = Array.from(ori)
            if (to.length === 1) {
                to[1] = to[0]
            }
            newPt[0] += to[0]
            newPt[1] += to[1]
        } else {
            newPt = Object.assign({}, ori);
            newPt.x += to.x
            newPt.y += to.y
        }
    }else{
        if (Array.isArray(ori)) {
            newPt = Array.from(ori)
            if (to.length === 1) {
                to[1] = to[0]
            }
            newPt[0] -= to[0]
            newPt[1] -= to[1]
        } else {
            newPt = Object.assign({}, ori);
            newPt.x -= to.x
            newPt.y -= to.y
        }
    }
    
    return newPt
}
const pn = () => {
    return Math.random() > 0.5 ? 1 : -1
}

function randomG(v) {
    var r = 0;
    for (var i = v; i > 0; i--) {
        r += Math.random();
    }
    return r / v;
}

function rNF(x) {
    return Math.floor(Math.random() * x)
}

function rMinMax(min, max) {
    return Math.random() * (+max - +min) + +min;
}

