function downloadBtn(){
    // let btn = new Path.Circle(project.view.bounds.bottomRight.subtract(20, 20), 10)
    var btn = new Path.RegularPolygon(project.view.bounds.bottomRight.subtract(20, 20), 3, 5);
    btn.fillColor = '#C2D3D5'
    btn.strokeWidth = 1
    btn.rotate(180)
    
    btn.onMouseEnter = function (e) {
        e.event.toElement.style.cursor = 'pointer'
        btn.fillColor.brightness -= 0.5
    }
    btn.onMouseLeave = function (e) {
        e.event.toElement.style.cursor = 'auto'
        btn.fillColor.brightness += 0.5
    }

    btn.onClick = function () {
        download()
    }
}





