    drawRectRotate(proID,num){
        if (num == null) num = 50
        paper.projects.forEach((pro) => {
            if (pro.name == proID) {
                pro.activate()
                project.clear()
            }
        })
        let [w, h] = [project.view.bounds.width, project.view.bounds.height]
        let vBD = project.view.bounds

        let count = 3
        let space = 0
        let ratio = 1
        let g = new Group()
        let size = new Size(w / count, w / count / ratio)
        // console.log(randomG(1800));
        for(let y=0;y<count;y++){
            for (let x = 0; x < count; x++) {
                
                let rec = new Path.Rectangle(size,size)
                rec.style={
                    fillColor: pn() > 0 ? '#2443CD' : '#CD6322',
                }
                rec.fillColor.hue += [x+y]*10
                rec.position.x += x * size.width + space * x
                rec.position.y += y * size.height + space * y
                rec.rotation = pn()>0?45:0
                   let g2 = new Group()

               if(Math.random()>0.5){
                   let countsScale = rNF(3)
                   let count2 = count + countsScale*pn()
                   for (let y2 = 0; y2 < count2; y2++) {
                       for (let x2 = 0; x2 < count2; x2++) {
                           let size2 = size.divide(count2)
                           let rec2 = new Path.Rectangle(rec.bounds.topLeft, size2)
                           rec2.style = {
                               fillColor: pn() > 0 ? '#2443CD' : '#CD6322',
                           }
                           rec2.blendMode = rb()?'multiply':'normal'
                        //    rec.fillColor.brightness += [x + y] / 50
                           rec2.position.x += x2 * size2.width + space * x2 
                           rec2.position.y += y2 * size2.height + space * y2 
                           rec2.rotation = pn() > 0 ? 45 : 0
                           
                           g2.addChild(rec2)
                           
                       }
                   }

                   g2.fitBounds(rec.bounds)
                   g2.position = rec.position
                   
               }
                
                g.addChild(new Group(rec,g2))
            }
        }
        g.fitBounds(vBD)
        g.scale(0.8)
        g.bounds.center = vBD.center
    },
    
