<template>
    <b-form inline>
      <label class="sr-only" v-bind:for="'input-'+inputID">Post</label>
      <b-input v-bind:id="'input-'+inputID" v-bind:type="inputType" min="0" max="100" class="mb-1 mr-sm-2 mb-sm-0" 
      v-bind:placeholder="holder" v-model="inputData" @change="typeCheck"></b-input>

      

      <b-button variant="primary" @click="sendData" v-show="inputType=='text'">Post</b-button>
    </b-form>
</template>

<script>
export default {
  name: "Input",
  props:{
    holder:String,
    inputType:{
      type:String,
      default:"input"
    },
    inputID:String
  },
  data: function() {
    return {
      inputData:null
    };
  },
  methods:{
      typeCheck:function(par){
        
          if(this.inputType === "range"){this.sendData(par)}
      },
      sendData:function (e) {
        // console.log(this.inputType);
          if(this.inputType === "range"){
            this.inputData = e
            this.$emit('sentData',this.inputData)
          }else{
            // e.preventDefault()
            if(this.inputData == null) this.inputData = "default"
            this.$emit('sentData',this.inputData)
            this.inputData = null
          }
          
      }
  }
};
</script>

