<template :in-viewport-once='false'>
  <b-container fluid class="mb-5">
    <hr>

  	<b-row align-v="end">
  	    <b-col cols='12'  class="ml-auto mr-auto mb-2" v-show="showInput" >
  	        <Input v-show="showInput" v-on:sentData="getData" v-bind:inputType="inputType" v-bind:inputID="canvasID" v-bind:holder="showInputHolder" class="inline"/>
        </b-col>
  	</b-row>

  	<b-row>
  	    <b-col sm="12" class="m-auto">
  	        <canvas v-bind:id="canvasID" class="large-canvas shadow " ></canvas>
  	    </b-col>
  	    <!-- <b-col lg="1" md="none" v-show="showInput"></b-col> -->
  	</b-row>
    <b-row>
      <b-col sm="12" class="m-auto mt-2 "  >

          <b-button @click="download" variant="primary">
            DOWNLOAD
          </b-button>

      </b-col>
    </b-row>
    
  </b-container>
</template>


<script>
import inViewport from 'vue-in-viewport-mixin'

import Input from "./Input";
// inViewport = require('vue-in-viewport-mixin');

export default {
  name: "PaperCanvas",
  mixins: [ inViewport ],

  components: {
    Input
  },
  props: {
    canvasID: String,
    showInput:false,
    way: Function,
    inputType:String,
    showInputHolder:{
        type:String,
        default:'type here'
    }
  },
  data:function(){
      return{
          inputData:null,
          downLoadLink:null,
          inViewport: {
	        	now: Boolean  , // Is some part of the component currently in the viewport?
	        	fully: Boolean ,// Is the component completely in the viewport?
	        	above: Boolean ,// Is any part of the component above the viewport?
	        	below: Boolean // Is any part of the component below the viewport?
	        }
    }
  },
  methods: {
    setUp: function() {},
    draw: function() {
    //   DrawTool.drawText(this.inputData);
        this.way(this.canvasID,this.inputData)
        
    },
    download:function(){
        let canvas = document.getElementById(this.canvasID)
        let imgLink = canvas.toDataURL("image/png");
        var link = document.createElement("a");
        link.download = 'hayden_paper';
        link.href = imgLink
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        
    },
    getData(data) {
      this.inputData = data
    //   console.log(data);
      this.draw()
    }
  },
  watch: {
 		'inViewport.now': function(visible) {
 			if(visible)this.draw()
 		}
 	},

  mounted: function() {
    // console.log(this.canvasID)
    // console.log(paper);
    paper.setup(this.canvasID);
    paper.project.name = this.canvasID
    // this.draw();
  }
};
</script>

