    setBoxColor:function(data){
      return `background-color:hsl(${data.hue*360},${data.saturation*100}%,${data.lightness*100}%)`
      
    },
    initPaperTexture:function(){
      const rNF = (x)=>{return Math.floor(Math.random()*x)}
      
      let canvas = document.createElement("CANVAS")
      canvas.id = 'textureCanvas'
        canvas.height = 512
        canvas.width = 512
        canvas.style.display = 'none'
        document.body.appendChild(canvas);
        paper.setup(canvas);
        paper.project.name = 'texts'
        this.boxTexture = new THREE.Texture(canvas)
        // this.boxTexture.wrapS = THREE.RepeatWrapping;
        this.boxTexture.wrapT = THREE.RepeatWrapping;
        this.boxTexture.rotation = Math.PI/2
        this.boxTexture.offset.x = -0.3
        this.boxTexture.repeat.y = - 1;

        var bg = new Path.Rectangle(0, 0, paper.view.bounds.width, paper.view.bounds.height)
        bg.fillColor = 'white'
        
        const textCount = 40
        for(let i=0;i<textCount;i++){
            var ranSize = Math.random() * 10 + 5
            var ranText
            if(this.allPosts){
              ranText = this.allPosts[rNF(this.allPosts.length)]
              // ranText = this.textCollection[rNF(this.textCollection.length)]
            }else{
              ranText = this.textCollection[rNF(this.textCollection.length)]
            }
                var text = new PointText(new Point(0, 0))
                text.position.y = 10*i + Math.random()*3
                text.position.x =0
                text.fontSize = ranSize
                // text.justification = 'center';
                text.fillColor = 'black';
                text.content = ranText
        }
    },
    updateTowerTexture:function(){
      const rNF = (x)=>{return Math.floor(Math.random()*x)}
        
        paper.projects.forEach((pjt)=>{
          if(pjt.name == 'texts') pjt.activate()
        })
        var allPath = project.activeLayer.children

        for (let i=0;i<allPath.length;i++){
         if(i===0)continue
         if (allPath[i].position.x > 512) {
                allPath[i].content = this.allPosts[rNF(this.allPosts.length)]
                allPath[i].position.x = 0 - allPath[i].bounds.width/2
            }
            allPath[i].position.x += i/30
        }
        this.boxTexture.needsUpdate = true
        
    },
    initSkyTexture(){
        this.skyCanvases = []
        for(let i=0;i<6;i++){
          let canvas = document.createElement("canvas")
          canvas.id = `skyCanvas${i}`
          // canvas.style.display = 'none'
          canvas.style.position = 'absolute'
          canvas.width = 512
          canvas.height = 512
          
          document.body.appendChild(canvas);
          paper.setup(canvas);
          paper.project.name = `skyCanvas${i}`

          let rect = new Path.Rectangle([0,0],[500,500])
          rect.fillColor = '#D0D0F7'
          rect.fitBounds(view.bounds)

          const count = {x:6,y:2}
          for(let y=0;y<count.y;y++){
            for(let x=0;x<count.x;x++){
              let light = new Path.Rectangle([0,view.bounds.center.y-55],[100,5])
              light.fillColor = '#CC6C2B'
              light.position.x += x * [100+30]
              light.position.y += y * [10+10]

            }
          }
          this.skyCanvases.push(canvas)
        }
       
        this.skyTexture = new THREE.CubeTexture(this.skyCanvases)
        this.skyTexture.minFilter = THREE.NearestFilter
        
        
    },

    // THREE
    initThree: function() {
        let container = document.getElementById('threeBox');
        let container2 = document.getElementById('threeBox2');
        this.w = container.clientWidth
        this.h = container.clientHeight
        let padding = 0
        let ratio = this.w/this.h
        let viewSize = 5
        // this.camera = new THREE.PerspectiveCamera(45, container.clientWidth/container.clientHeight, 1, 1000);
        this.camera = new THREE.OrthographicCamera(-ratio*viewSize/2, ratio*viewSize/2, 1*viewSize / 2, -1*viewSize / 2, -1000, 1000);
        this.camera.position.set(3,2,5)

        
        this.camera.updateMatrix();
        this.camera.updateMatrixWorld();
  

        //to scroll
        var scrollScene = new ScrollMagic.Scene({triggerElement:'#triggerCameraZoomIn',duration:4 * window.innerHeight})
        // .addIndicators() // add indicators (requires plugin)
        .addTo(this.scrollController)
        .on("progress", function (e) {
            
              this.camera.rotation.y = Math.PI/3-Math.PI*e.progress
              this.camera.position.x = 6-e.progress * 10
              this.camera.position.y = 0-e.progress * 10
              this.camera.position.z = 0+e.progress * 10
       
            this.camera.updateMatrix();
            this.camera.updateMatrixWorld();
          }.bind(this))
        .on("enter",function(e){
        }.bind(this))
        

