         if(e.progress>0&&e.progress<0.08){
           this.sceneIntroB = true
         }else{
           this.sceneIntroB = false
         }
         if(e.progress< 0.4 && e.progress > 0.07){
           this.showSceneBIntro = true
           if(this.circleLine){
             this.circleLine.attr({
             "stroke-dashoffset":`${-Math.sin(e.progress)*1500*8}`
              })

              this.webApiSvg.selectAll('.webApiBar').forEach((i,index)=>{
                i.attr({
                  width:`${Math.abs(Math.sin(e.progress*10*perlin.noise.perlin2(0.3,index/10)))*150}`
                })
              })

           }
           
         }else{
           this.showSceneBIntro = false
           if(this.circleLine){
             this.circleLine.attr({
             "stroke-dashoffset":`${this.circleLine.getTotalLength()}`
            })
            perlin.noise.seed(Math.random())
           }
         }

         if(e.progress< 0.8 && e.progress > 0.4){
           this.showBarB = true
          //  this.glitchPass.enabled = true
          //  this.glitchPass.curF++
           
         }else{
           this.showBarB = false
          //  this.glitchPass.enabled = false
           this.glitchPass.seed = e.progress
           this.showScreenB2open = false
         }

      

         

         for(let i=1;i<3+1;i++){
            if(e.progress<[1+i]/10+0.1&&e.progress>i/10+0.1){
            if(this.loadBDone)this.sceneB.getObjectByName(`screenA${4-i-1}`).material.emissiveIntensity = 0.8
            if(this.loadBDone)this.sceneB.getObjectByName(`screenA${4-i-1}`).material.color = new THREE.Color(`hsl(${[i+1]*40},70%,70%)`)
          }else{
            if(this.loadBDone)this.sceneB.getObjectByName(`screenA${4-i-1}`).material.emissiveIntensity = 0.1
          }
         }

         for(let i=1;i<3+1;i++){
            if(e.progress< [i+0.95]/10+0.35&&e.progress>i/10+0.35){
            if(i==1&&this.tempLevel){
              
              if(this.tempLevel.attr("height") < [this.weatherData.data.main.temp-273.15]*10){
                this.tempLevel.attr({
                height:"+=5",
                y:'-=5'
              })
              }
              
            }  
              
            if(this.loadBDone)this.sceneB.getObjectByName(`screenB${i-1}`).material.emissiveIntensity = 0.5
            let color 
            switch(i-1){
              case 0:
                color = '#fc4a1a'
                break;
              case 1:
                color = '#4abdac'
                break;
              case 2:
                color = '#f7b733'
                break;    
            }

            if(this.loadBDone)this.sceneB.getObjectByName(`screenB${i-1}`).material.emissive = new THREE.Color(color)
            this[`showScreenB${i-1}`] = true
          }else{
            if(this.loadBDone)this.sceneB.getObjectByName(`screenB${i-1}`).material.emissiveIntensity = 0.1
            this[`showScreenB${i-1}`] = false
            if(i==2){this.showScreenB2open = false}
            //还原

            //svg ani
            if(this.tempLevel){
               if(this.tempLevel.attr("height") > 10){
              this.tempLevel.attr({height:"-=1",y:"+=1"})
              }
            }

            
            //
          }
         }
         
         
         if(e.progress < 0.8){
          this.cameraB.position.y = e.progress * 2
          this.cameraB.position.z = 1-e.progress  * 2
          this.cameraB.position.x = e.progress*10*2
          this.cameraB.rotation.y = Math.PI*e.progress
