    processColor:function(data){
        // this.sortedFirstDetection = Object.entries(data[0]['expressions']).sort((a,b)=>{return b[1]-a[1]}).slice(0,3)
        let finalHue = 0
        let finalSaturation =0
        let finalLightless =0
        let finalInfluence = []
        data.forEach((detection)=>{

          detection = Object.entries(detection['expressions']).sort((a,b)=>{return b[1]-a[1]}).slice(0,3)
          let allHue = []
          let allSaturation = []
          let allLightless = []
          
          detection.forEach((emotion)=>{
            let hue =0
            let intensity = 0
            let saturation = 0
            switch(emotion[0]){
              case "neutral":
                hue = 0
                intensity = emotion[1]/3
                saturation = 0.3
                break;
              case "angry":
                hue = 0.4
                intensity = emotion[1]*2
                saturation = 0.65
                break;
              case "surprised":
                hue = 0.3
                intensity = emotion[1]
                saturation = 0.85
                break;
              case "sad":
                hue = 0.6
                intensity = emotion[1]*5
                saturation = 0.25
                break;
              case "disgusted":
                hue = 0.8
                intensity = emotion[1]*3
                saturation = 0.4
                break;
              case "happy":
                hue = 0.1
                intensity = emotion[1]
                saturation = 0.75
                break;
              case "fearful":
                hue = 0.7
                intensity = emotion[1]*8
                saturation = 0.3
                break;
            }

            let hueWithIntensity = hue * intensity
            allHue.push(hueWithIntensity)
            allSaturation.push(saturation)
          
          })

          let personValue = {hue:0,saturation:0,lightless:0}

          for(let i of allHue){
            personValue.hue += i
          }

          // for(let i of allSaturation){
          //   personValue.saturation += i
          // }
          
          // for(let i of allLightless){
          //   personValue.lightless += i
          // }
          
          personValue.hue /= allHue.length
          personValue.saturation = Math.max(...allSaturation)
          personValue.lightless = Math.max(...allSaturation)
          finalInfluence.push(personValue)
        })
        
        for(let i of finalInfluence){
          // console.log(i);
          finalHue += i.hue
          finalSaturation += i.saturation
          finalLightless += i.lightless
        }

        finalHue /= finalInfluence.length
        finalSaturation /= finalInfluence.length
        finalLightless /= finalInfluence.length
        // console.log(finalHue);
        
        return {hue:finalHue,saturation:finalSaturation,lightness:finalLightless}
        
        
    },
    tweenChange: function(currentLight){
        if(changeLightColor) changeLightColor.stop()
        this.proceesed = this.processColor(this.detections)
        let color = new THREE.Color()
        currentLight.color.getHSL(color)
        
        let changeLightColor = new TWEEN.Tween(color)
        .to({h:this.proceesed.hue,s:this.proceesed.saturation,l:0.5},500)
        .easing(TWEEN.Easing.Quadratic.In)
        .onUpdate(()=>{


          this.light.color.setHSL(...Object.values(color))
        })
        changeLightColor.start()
      },
