    createGridIndex:function(){
        let coor = {x:2,y:3,z:3}
        let space = 0.05
        this.boxGridCoor = []
        for(let z=0;z<coor.z;z++){
          for(let x=coor.x;x>0;x--){
          for(let y=0;y<coor.y;y++){
              let boxCoor = {}

               boxCoor.x = x + space*x + 1
               boxCoor.y = y + space*y
               boxCoor.z = z + space*z +1
               this.boxGridCoor.push(boxCoor)
            }
          }
        }
        this.boxStack = new THREE.Group()
        this.scene.add(this.boxStack)

      },
      createNewTexture:function(){
        let mainEmotion
        let mainEmotionValue

        if(this.detections.length>0){
          this.proceesed = this.processColor(this.detections)
          mainEmotion = Object.entries(this.detections[0]['expressions']).sort((a,b)=>{return b[1]-a[1]}).slice(0,3)[0][0]
          mainEmotionValue = Object.entries(this.detections[0]['expressions']).sort((a,b)=>{return b[1]-a[1]}).slice(0,3)[0][1]
        }else{
          this.proceesed = {hue:Math.random(),saturation:Math.random(),lightness:Math.random()}
          mainEmotion = 'neutral'
          mainEmotionValue = '0.9'
        }

        let canvas = document.createElement("canvas")
        canvas.id = `canvas-${this.boxIndex}` 
        canvas.height = 512
        canvas.width = 512
        canvas.style.display = 'none'
        document.body.appendChild(canvas);
        paper.setup(canvas);
        let name = `dynamicTexture${this.boxIndex}`
        paper.project.name = name
        
        
        // Methods.onHappy(name)
        
        
        switch(mainEmotion){
          case 'happy':
            Methods.onHappy(name,this.proceesed,mainEmotionValue)
            break;
          case 'neutral':
            Methods.onNeutral(name,this.proceesed,mainEmotionValue)
            break;
          case 'sad':
            Methods.onSad(name,this.proceesed,mainEmotionValue)
            break;  
          case 'surprised':
            Methods.onSurprised(name,this.proceesed,mainEmotionValue)
            break;  
          case 'disgusted':
            Methods.onDisgusted(name,this.proceesed,mainEmotionValue)
            break;  
          case 'fearful':
            Methods.onFear(name,this.proceesed,mainEmotionValue)
            break;   
          default:
            // Methods.onSad(name,this.proceesed,mainEmotionValue)
            Methods.onSurprised(name,this.proceesed,mainEmotionValue)
            break;
        }
        return canvas
      },
