      createNewTv:function(mesh){
        let canvas = this.createNewTextureD()
        this.tvTextures[this.tvIndex] = new THREE.Texture(canvas)
        this.tvTextures[this.tvIndex].needsUpdate = true
        document.body.removeChild(canvas)
        let cpMtr = mesh.material.clone() 
        cpMtr.map = this.tvTextures[this.tvIndex]
        cpMtr.emissiveMap = this.tvTextures[this.tvIndex]
        mesh.material = cpMtr

        this.tvIndex++
        if(this.tvTextures.length===50){
          this.tvTextures = []
        } 
        if(this.tvIndex === 50){
          this.tvIndex = 0
        }
      },
    createNewBox:function(){
        let cpMesh = this.mesh.clone()
        cpMesh.name = `box${this.boxIndex}`
        
        let canvas = this.createNewTexture()
        
        
        this.dynamicTextures[this.boxIndex] = new THREE.Texture(canvas)
        this.dynamicTextures[this.boxIndex].wrapT = this.dynamicTextures[this.boxIndex].wrapS =THREE.ClampToEdgeWrapping
        this.dynamicTextures[this.boxIndex].needsUpdate = true
        this.sideImgSrc = canvas.toDataURL()
        document.body.removeChild(canvas)
        let cpMtr = cpMesh.material.clone() 
        cpMtr.map = this.dynamicTextures[this.boxIndex]
        cpMtr.emissiveMap = this.dynamicTextures[this.boxIndex]
        cpMesh.material = cpMtr


        this.g.add(cpMesh)
        this.movingBox(cpMesh,this.boxIndex)
        this.boxesData.push(this.processColor(this.detections))
        // console.log(this.processColor(this.detections));
        
        this.boxIndex++
        if(this.dynamicTextures.length===18){
          this.dynamicTextures = []
          this.boxesData = []
        } 
        if(this.boxIndex === 18){
          this.boxIndex = 0
        }

      },
      
    animate: function(time=0) {
        
          time++
        
          requestAnimationFrame(this.animate);
        
        if(this.animateRun){

        TWEEN.update(time)

        
         if(this.showBarB&&this.boxTexture&&this.inSceneB){
          this.sceneBCube.rotation.z += 1/600
          this.sceneBCube.rotation.y -= 1/600
          this.faceA.material.emissiveIntensity = Math.abs(Math.sin(time/600)*3)
          this.updateTowerTexture()
          }

        if(this.inSceneA){
          if(this.mouse) this.light.position.set(5+this.mouse.x*10,3.5,5+this.mouse.y*10)
        }

        if(this.dotsIsUpdate){
          this.dots.forEach((dot)=>{
          dot.update(this.mouse.x*2,this.mouse.y*2)
          })
        }

        if(this.inSceneD&&this.lightDG&&this.cameraD){

          this.lightDG.position.x -= 1
          if(this.lightDG.position.x<-100) this.lightDG.position.x = 100
          this.cars.children.forEach((car,i)=>{
            let k=1
            if(i%2==0) k=-1
          car.position.y = Math.sin(time/100)*0.01*k
          car.position.z = Math.sin(time/100)*0.01*k

          })

          this.dots2.forEach((dot,i)=>{
            dot.update()
            if(dot.shouldRemove){this.dots2.splice(i,1)}
          })
        }
        
        // this.dots.update()

        this.composer.render();
        }
        
    },
