    initWeatherSvg:function(){
      if(this.currentWeatherImg) this.currentWeatherImg.remove()
      
      let s = Snap('#svgWeather')
      Snap.load('models/weatherCombined.svg',svgLoad.bind(this))

      function svgLoad(data){
        s.append( data );
        let currentWeather = this.weatherData.data.weather[0].main
        let shape
        switch(currentWeather){
          case 'Rain':
            shape = s.select('#rain');
          break;
          case 'Clear':
            shape = s.select('#clear');
          break;
          case 'Mist':
          case 'Fog':
          case 'Haze':
            shape = s.select('#mist');
          break;
          case 'Clouds':
            shape = s.select('#cloudy');
          break;
          case 'Drizzle':
            shape = s.select('#drizzle')
          break;
          default:
            shape = s.select('#sun');
          break;
        }
        this.currentWeatherImg = shape
        this.currentWeatherImg.attr({
          transform:'translate(-50,0)'
        })
        s.append(this.currentWeatherImg)
      }

    },
