    getPosts:function(keyWord){
      this.axios.get('http://localhost:3000/api/twitter',{
        params: {
          content:keyWord
        }
      }).then((res)=>{
        let allPosts = res.data['statuses']
        let allText = []
        for(let i in allPosts){
          allText.push(allPosts[i].text)
        }
        this.allPosts = allText
        // console.log(this.allPosts);
      })
      
    },
    getWeather:function(){
      this.axios.get(`https://api.openweathermap.org/data/2.5/weather?id=${2643741}&APPID=82161b512fc36f943b2cf28bf4f7df2a`)
      .then((res)=>{
        this.weatherData = res
        // console.log(res);
        let feeling 
        let temp = parseFloat(res.data.main.temp-273.15)
        switch(true){
          case (temp>26):
            feeling = 'hot'
            break;
          case (temp < 26 && temp > 18):
            feeling = 'comfortable'
            break;
          case (temp < 18 && temp > 10):
            feeling = 'cold'
            break;
          case (temp < 10):
            feeling = 'freezing'
            break;
          default:
            feeling = 'warm'

        }
        if(Math.random()>0.5){
          this.getPosts(res.data.weather[0].main);
        }else{
          this.getPosts(feeling);
        }
      })

    },
    getTextAnalyzed:function(){
      let string = []
      for(let str in this.allPosts.slice(0,10)){
        string.push(this.allPosts[str])
      }
      let text = string.toString().replace(/[^a-z0-9]+/gi,' ')
      let json =  `{"texts":["${text}"]}`

      $.ajax({
        
	    	type:'post',
	    	url:'https://api.indatalabs.com/v1/text?apikey=23dbe59e-1887-4b56-8349-426b5dbc187f&models=sentiment,interests,age_group',
	    	contentType: "application/json",
	    	dataType: 'json',
	    	data: json,
	    	success:function (response) {
	    		let data = response.response[0]
	    		// console.log(data);
          this.analyzedText = data.sentiment
	    		
    
	    	}.bind(this)
	    })
    
    },
