   //to mou
  mounted(){
    
    if(this.loop)clearInterval(this.loop)
    this.scrollController = new ScrollMagic.Controller()
    var scrollScene = new ScrollMagic.Scene({triggerElement:'#trigger',duration:15000})
    .setPin("#threeBoxContainer")
    // .addIndicators({name: "leave three"}) // add indicators (requires plugin)
    .addTo(this.scrollController)
    .on("progress",function(e){
      this.pageProgress = e.progress
      if(this.sceneBgc){
         this.scene.background = new THREE.Color(`hsl(${Math.abs(Math.sin(this.sceneBgc.h+e.progress))*180},5%,${parseInt(e.progress*30+30)}%)`)
          this.sceneB.background = this.scene.background
          this.sceneC.background = this.scene.background
          this.sceneD.background = new THREE.Color(`hsl(${Math.abs(Math.sin(this.sceneBgc.h+e.progress))*180},5%,${parseInt(50-Math.min(50,e.progress*70))}%)`)
          this.sceneE.background = new THREE.Color(`hsl(${Math.abs(Math.sin(this.sceneBgc.h+e.progress))*180},5%,${parseInt(Math.min(Math.max(0,[e.progress-0.75]*500),50))}%)`)
      }
    }.bind(this))
    .on("enter",function(e){
      this.animateRun  = true

    }.bind(this))
    .on("leave",function(e){
      if(e.scrollDirection=="REVERSE"){
        this.showEmotion = false
      } else{
      this.animateRun  = false

      }


    }.bind(this))

    this.getWeather()
    this.initWeatherSvg()
    this.initPaperTexture()
    this.initSceneCInputSvg()

    this.initThree()
    this.createGridIndex()
    this.video = document.getElementById('video')
    this.video.play()

    navigator.mediaDevices.getUserMedia({video:{}})
        .then(stream=> this.video.srcObject = stream )
        .catch(err=>console.error(err))

    paper.setup(document.getElementById('indexCanvas'))
    this.createPaperInIndex('index',document.getElementById('indexCanvas'))
    setInterval(()=>{
      if(this.loopOff)this.updatePaperInIndex()
    },10000)
    this.initTempSvg()
    this.initWebAPISvg()


    this.activeDetection = setInterval(()=>{
        if(!this.userIsActive){
          console.log('user could have been left');
          this.autoHidden = true
        }else{
          console.log('user is active');
          this.autoHidden = false
        }
    },10000)

    this.userIsNotActive = setInterval(() => {
      this.userIsActive = false
    }, 5000);

    window.addEventListener("mousemove",function(e){
      this.userIsActive = true
      this.autoHidden = false
    }.bind(this))

    window.addEventListener("scroll",function(e){
      this.userIsActive = true
      this.autoHidden = false
    }.bind(this))
    
    Promise.all([
        faceapi.nets.tinyFaceDetector.load('./models'),
        faceapi.nets.faceExpressionNet.load('./models'),
        // this.audioBgc = new Howl({src:['./models/wiredbgc.flac']}),
        // this.audioFlash = new Howl({src:['./models/cameraWithFlash.wav']}),
        this.audioDida = new  Howl({src:['./models/click.mp3']}),
        this.audioLightOn = new Howl({src:['./models/lightOn.mp3']}),
        this.audioMachine = new Howl({src:['./models/machine.mp3']}),
        this.audioPop = new Howl({src:['./models/pop.wav']}),
        this.audioPopB = new Howl({src:['./models/popB.mp3']}),
        this.audioTrain = new Howl({src:['./models/train.mp3'],loop:true,volume:0.5}),
        
    ])
    .then(()=>{
      // this.audioFlash.loop = false
      this.audioTrain.loop = true
      this.audioTrain.autoplay = false
      this.audioMachine.volume(0.5)
    })
    .then(()=>{
      var startRender = new ScrollMagic.Scene({triggerElement:'#triggerStartLighting',duration:1000})
        // .addIndicators({name:'begin render'}) // add indicators (requires plugin)
        .addTo(this.scrollController)
        .on("progress",function(e){

          if(e.progress > 0.5 && e.progress < 0.7){
            this.sceneIntroA = true
          }else{
            this.sceneIntroA = false
          }
          
        }.bind(this))
        .on("enter", function (e) {
          this.animateRun  = true

          this.inSceneA = true
          this.loopOff = false
          if(this.firstBegin){
            this.firstBegin = false
            this.projectStart()
          }else{
            if(this.loopOff) this.loopOff = false
          }
        }.bind(this))
       .on("leave",function(e){
          if(e.scrollDirection === 'REVERSE'){
            this.loopOff = true
            this.animateRun = false
          }
      this.updatePaperInIndex()

        }.bind(this))  
    })
    .then(this.animate())

    this.projectStart = ()=>{
        console.log('project started');
        
        this.loopOff = true
        if(this.loop)clearInterval(this.loop)
        if(this.muted===false)this.audioLightOn.play()

        this.loop = setInterval(async () => {
          if (!this.loopOff){
            let detections
            if(this.allowToUseCamera){
              detections = await faceapi.detectAllFaces(this.video, new faceapi.TinyFaceDetectorOptions({scoreThreshold:0.5})).withFaceExpressions()
            }else{
              detections = this.fakeData()
            }

            
            if(detections.length>0){
                this.detections = detections
                this.noFace = false
                // console.log(this.detections);
                this.takingPhoto = true
                await setTimeout(()=>{
                  this.takingPhoto = false
                },1000)
                if(!this.cameraLoading)this.createNewBox()
              }else{
                this.noFace = true
                this.takingPhoto = false
                this.detections = null
              }

              this.cameraLoading = false
          }
          
          
          this.lastDetections = this.detections[0]
              
        }, 3000)
        
        this.loopWeather = setInterval(()=>{
          if(this.showBarB) this.getWeather()
        },10000)
    }

    
  },
  
};
